<div class="city"><div class="name"><h1>{{ COUNTYNAME }}</h1><h2>{{ COUNTYENG }}</h2></div><div class="weather" v-if="weatherData"><h1>{{ weatherData.datasetDescription }}</h1><div v-for="item in weatherData.location[0].weatherElement[0].time"><div class="text">開始時間 : {{item.startTime}}</div><div class="text">結束時間 : {{item.endTime}}</div><div class="text">天氣 : {{item.parameter.parameterName}}</div><div class="line"></div></div></div></div>